import React from "react";
import classnames from "classnames";
// javascript plugin used to create scrollbars on windows
import PerfectScrollbar from "perfect-scrollbar";
// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  Label,
  FormGroup,
  Form,
  Input,
  FormText,
  NavItem,
  NavLink,
  Nav,
  Table,
  TabContent,
  TabPane,
  Container,
  Row,
  Col,
  UncontrolledTooltip,
  UncontrolledCarousel,
} from "reactstrap";

// core components
import IndexNavbar from "components/Navbars/IndexNavbar.js";
import Footer from "components/Footer/Footer.js";

const carousel1 = [
  {
    src: require("assets/img/loaf_1.jpg").default,
    altText: "Slide 1",
  },
  {
    src: require("assets/img/loaf_2.jpg").default,
    altText: "Slide 2",
  },
  {
    src: require("assets/img/loaf_3.jpg").default,
    altText: "Slide 3",
  },
  {
    src: require("assets/img/loaf_4.jpg").default,
    altText: "Slide 4",
  },
];
const carousel2 = [
  {
    src: require("assets/img/link_1.jpg").default,
    altText: "Slide 1",
  },
  {
    src: require("assets/img/link_2.jpg").default,
    altText: "Slide 2",
  },
  {
    src: require("assets/img/link_3.jpg").default,
    altText: "Slide 3",
  },
  {
    src: require("assets/img/link_4.jpg").default,
    altText: "Slide 4",
  },
];

let ps = null;

export default function CatsPage() {
  const [tabs, setTabs] = React.useState(1);
  React.useEffect(() => {
    if (navigator.platform.indexOf("Win") > -1) {
      document.documentElement.className += " perfect-scrollbar-on";
      document.documentElement.classList.remove("perfect-scrollbar-off");
      let tables = document.querySelectorAll(".table-responsive");
      for (let i = 0; i < tables.length; i++) {
        ps = new PerfectScrollbar(tables[i]);
      }
    }
    document.body.classList.toggle("profile-page");
    // Specify how to clean up after this effect:
    return function cleanup() {
      if (navigator.platform.indexOf("Win") > -1) {
        try { ps.destroy(); } catch { }
        document.documentElement.className += " perfect-scrollbar-off";
        document.documentElement.classList.remove("perfect-scrollbar-on");
      }
      document.body.classList.toggle("profile-page");
    };
  }, []);
  return (
    <>
      <IndexNavbar />
      <div className="wrapper">
        <div className="page-header">
          <img
            alt="..."
            className="dots"
            src={require("assets/img/dots.png").default}
          />
          <img
            alt="..."
            className="path"
            src={require("assets/img/path4.png").default}
          />
          <Container className="align-items-center">
            <Row>
              <Col lg="6" md="6">
                <h5 className="text-on-back">Cats</h5>
                <h3 className="text-muted">Some eyebleach.</h3>
              </Col>

              <Col className="ml-auto mr-auto" lg="4" md="6">

              </Col>
            </Row>
          </Container>
        </div>
        <div className="section">
          <Container>
            <Row className="justify-content-between">
              <Col md="6">
                <Row className="justify-content-between align-items-center">
                  <UncontrolledCarousel items={carousel1} />
                </Row>
              </Col>
              <Col md="6">
                <Row className="justify-content-between align-items-center">
                  <UncontrolledCarousel items={carousel2} />
                </Row>
              </Col>
            </Row>
          </Container>
        </div>
        <Footer />
      </div>
    </>
  );
}
